import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import * as moment from 'moment';
import { ExportToExcelService } from 'src/app/core/services/common/export-to-excel.service';
import { CustomerService } from 'src/app/core/services/customer/customer.service';
import { PagerService } from 'src/app/core/services/pagination/pager.service';
import { SupplierService } from 'src/app/core/services/supplier/supplier.service';

@Component({
  selector: 'app-manage-refund',
  templateUrl: './manage-refund.component.html',
  styleUrls: ['./manage-refund.component.scss'],
})
export class ManageRefundComponent implements OnInit {
  arrSupplier: any = [];
  arrAllCustomers: any = [];
  // arrAllSuppliers: any = [];
  // pagination
  pager: any = {};
  intTotalCount = 0;
  intPageLimit = 50;
  intSkipCount = 0;
  fetchLoading = false;

  myForm: FormGroup;

  constructor(
    private supplierService: SupplierService,
    private customerService: CustomerService,
    private exportToExcelService: ExportToExcelService,
    private formBuilder: FormBuilder,
    private pageServiceObj: PagerService
  ) {
    this.myForm = this.formBuilder.group({
      supplier: [''],
      customer: [null],
      supplierCode: [''],
      phone: [''],
      paymentStatus: ['PENDING'],
    });
  }

  ngOnInit() {
    this.getAllRefundPayment();
    this.getAllCustomerAll();
    // this.getAllSupplierAll();
  }

  setPage(page) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.pager = this.pageServiceObj.getPager(
      this.intTotalCount,
      page,
      this.intPageLimit
    );
    this.getAllRefundPayment();
  }

  getAllCustomerAll() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      fkCustomerId: '',
      strName: '',
      cust_Code: '',
      fromDate: '',
      endDate: '',
      intSkipCount: '0',
      intPageLimit: '1000000',
    };
    this.customerService.getAllCustomer(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrAllCustomers = res.data.map((d) => {
          return {
            ...d,
            displayName: d.cust_Code + ' - ' + d.strName,
          };
        });
      } else {
        this.arrAllCustomers = [];
      }
    });
  }

  onCustomerChange() {
    const supplier = this.arrAllCustomers.find((e) => {
      return e.cust_Code === this.myForm.value.customer;
    });

    this.myForm.patchValue({
      supplier: supplier.supplier_Name,
      supplierCode: supplier.sup_Code,
    });
  }
  // getAllSupplierAll() {
  //   const obj = {
  //     strLoginUserId: localStorage.getItem('userId'),
  //     fkSupplierId: '',
  //     strName: '',
  //     sup_Code: '',
  //     fromDate: '',
  //     endDate: '',
  //     intSkipCount: '0',
  //     intPageLimit: '1000000',
  //   };
  //   this.supplierService.getAllSupplier(obj).subscribe((res) => {
  //     this.fetchLoading = false;
  //     if (res.success) {
  //       this.arrAllSuppliers = res.data.map((d) => {
  //         return {
  //           ...d,
  //           displayName: d.sup_Code + ' - ' + d.strName,
  //         };
  //       });
  //     } else {
  //       this.arrAllSuppliers = [];
  //     }
  //   });
  // }

  getFromCount() {
    return this.intPageLimit * (this.pager.currentPage - 1) + 1;
  }

  getToCount() {
    return (
      this.intPageLimit * (this.pager.currentPage - 1) + this.arrSupplier.length
    );
  }
  getAllRefundPayment() {
    let skipCount = this.intSkipCount;
    if (this.pager.intSkipCount) {
      skipCount = this.pager.intSkipCount;
    }
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: skipCount,
      intPageLimit: this.intPageLimit,
      fkSupplierId: '',
      cust_Code: this.myForm.value.customer,
      sup_Code: this.myForm.value.supplierCode,
      strPhone: this.myForm.value.phone,
      strName: '',
      payment_Status: this.myForm.value.paymentStatus,
      strCountry: '',
    };

    this.fetchLoading = true;
    this.supplierService.getAllRefundListing(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrSupplier = res.data;

        this.intTotalCount = res.count;

        this.pager = this.pageServiceObj.getPager(
          this.intTotalCount,
          this.pager.currentPage,
          this.intPageLimit
        );
      } else {
        this.arrSupplier = [];
        // alert(res.message);
      }
    });
  }

  downloadExcel() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1000000,
      fkSupplierId: '',
      sup_Code: this.myForm.value.supplierCode,
      cust_Code: this.myForm.value.customer,
      strPhone: this.myForm.value.phone,
      strName: '',
      payment_Status: this.myForm.value.paymentStatus,
      strCountry: '',
    };

    this.fetchLoading = true;
    this.supplierService.getPaymentSupplier(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        let jsonData: any = [];

        res.data.forEach((e) => {
          e.paymentDetails.forEach((payment, i) => {
            let obj: any = {};
            if (i === 0)
              obj = {
                'Supplier Code': e.sup_Code,
                'Supplier Name': e.supplierName,
                Phone: e.supplierPhone,
                Country: e.supplierCountry,
                Refund: e.Refund_Amount,
                'Paid Amount': e.Total_Paid_Amount,
                'G-Voucher Credit': e.Service_Credit_Amount,
                'G-Voucher Debit': e.Service_Debit_Amount,
                'Total Amount': e.Total_Amount,
                Balance: e.Total_Amount - e.Total_Paid_Amount,
              };
            else
              obj = {
                'Supplier Code': '',
                'Supplier Name': '',
                Phone: '',
                Country: '',
                Refund: '',
                'Paid Amount': '',
                'G-Voucher Credit': '',
                'G-Voucher Debit': '',
                'Total Amount': '',
                Balance: '',
              };
            obj['Payment Code'] = payment.payment_Code;
            obj['Payment Type'] = payment.type;
            obj['Transfer Mode'] = payment.transferMode;
            obj['Bank'] = payment.bank;
            obj['Payment Status'] = payment.paymentStatus;
            obj['Amount'] = payment.amount;
            obj['Date'] = moment(payment.date).format('DD-MM-YYYY');
            obj['Description'] = payment.description;
            jsonData.push(obj);
          });
        });

        // console.log('jsonData', jsonData);
        this.exportToExcelService.exportAsExcelFile(
          jsonData,
          'Supplier Payments '
        );
      }
    });
  }
}
