import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { CustomerService } from 'src/app/core/services/customer/customer.service';
import { SupplierService } from 'src/app/core/services/supplier/supplier.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-create-refund',
  templateUrl: './create-refund.component.html',
  styleUrls: ['./create-refund.component.scss'],
})
export class CreateRefundComponent {
  arrAllCustomers: any = [];
  arrAllSupplier: any = [];
  arrTransferMode: any = [];
  arrBanks: any = [];
  // arrPaymentType: any = [
  //   {
  //     type: 'G-Voucher (Credit)',
  //     sentType: 'SERVICE',
  //     mode: 'Credit',
  //   },
  //   {
  //     type: 'G-Voucher (Debit)',
  //     sentType: 'SERVICE',
  //     mode: 'Debit',
  //   },
  //   {
  //     type: 'PAID',
  //     sentType: 'PAID',
  //     mode: '',
  //   },
  // {
  //   type: 'REFUND',
  // },
  // ];
  myForm: FormGroup;
  submitted: boolean = false;
  isLoading = false;

  constructor(
    private customerService: CustomerService,
    private supplierService: SupplierService,
    private toastr: ToastrService,
    private formBuilder: FormBuilder,
    private router: Router
  ) {
    this.myForm = this.formBuilder.group({
      customer: [null, Validators.required],
      supplierId: [''],
      supplier: [''],
      totalAmount: [0, Validators.required],
      paidAmount: [0, Validators.required],
      cost_Price: [0, Validators.required],
      Service_Credit_Amount: ['0', Validators.required],
      Service_Debit_Amount: ['0', Validators.required],
      Refund_Amount: ['0', Validators.required],
      paymentDetails: this.formBuilder.array([]),
    });

    this.getAllCustomerAll();
    // this.getAllSupplierAll();
    this.getMetaData();
    this.getMetaDataBank();
  }

  get f() {
    return this.myForm.controls;
  }

  get paymentDetails(): FormArray {
    return this.myForm.get('paymentDetails') as FormArray;
  }

  newPaymentDetails(): FormGroup {
    return this.formBuilder.group({
      // type: [null, Validators.required],
      mode: [''],
      transferMode: [null, Validators.required],
      transferPercent: [''],
      bank: [null],
      amount: ['', Validators.required],
      date: ['', Validators.required],
      description: [''],
    });
  }

  removeItem(i: number) {
    Swal.fire({
      title: 'Do you want to remove?',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      reverseButtons: true,
      confirmButtonColor: '#dc3741',
    }).then((result) => {
      if (result.isConfirmed) {
        this.paymentDetails.removeAt(i);
      }
    });
  }

  addItem() {
    if (this.f.customer.value === '' || this.f.customer.value === null) {
      this.toastr.warning('', 'Choose a Customer');
      return;
    }
    this.paymentDetails.push(this.newPaymentDetails());
  }

  getAllCustomerAll() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      fkCustomerId: '',
      strName: '',
      cust_Code: '',
      fromDate: '',
      endDate: '',
      intSkipCount: '0',
      intPageLimit: '1000000',
    };
    this.customerService.getAllCustomer(obj).subscribe((res) => {
      if (res.success) {
        this.arrAllCustomers = res.data.map((d) => {
          return {
            ...d,
            displayName: d.cust_Code + ' - ' + d.strName,
          };
        });
      } else {
        this.arrAllCustomers = [];
      }
    });
  }
  getAllSupplierAll() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      fkCustomerId: '',
      strName: '',
      cust_Code: '',
      fromDate: '',
      endDate: '',
      intSkipCount: '0',
      intPageLimit: '1000000',
    };
    this.supplierService.getAllSupplier(obj).subscribe((res) => {
      if (res.success) {
        this.arrAllSupplier = res.data.map((d) => {
          return {
            ...d,
            displayName: d.sup_Code + ' - ' + d.strName,
          };
        });
      } else {
        this.arrAllSupplier = [];
      }
    });
  }

  onTransferChange(val, i) {
    this.paymentDetails.at(i).patchValue({ transferPercent: val.percentage });
  }

  onChangeCustomer() {
    this.myForm.patchValue({
      totalAmount: this.myForm.value.customer.Total_Amount,
      cost_Price: this.myForm.value.customer.cost_Price,
      supplier: this.myForm.value.customer.supplier_Name,
      supplierId: this.myForm.value.customer.fkSupplierId,
      paidAmount: this.myForm.value.customer?.Total_Paid_Amount || 0,
      Refund_Amount: this.myForm.value.customer?.Refund_Amount || 0,
      Service_Credit_Amount:
        this.myForm.value.customer?.Service_Credit_Amount || 0,
      Service_Debit_Amount:
        this.myForm.value.customer?.Service_Debit_Amount || 0,
    });
  }

  onPaymentChange(event, i) {
    this.paymentDetails.at(i).patchValue({ mode: event.mode });
  }

  resetForm() {
    this.myForm.reset();
    this.submitted = false;
  }

  getMetaData() {
    const obj = {
      metaType: 'transfermod', // Amentities transfermod
    };

    this.supplierService.metaData(obj).subscribe((data) => {
      if (data.success) {
        this.arrTransferMode = data.data;
      } else {
        this.arrTransferMode = [];
      }
    });
  }
  getMetaDataBank() {
    const obj = {
      metaType: 'BANK', // Amentities transfermod
    };

    this.supplierService.metaData(obj).subscribe((data) => {
      if (data.success) {
        this.arrBanks = data.data;
      } else {
        this.arrBanks = [];
      }
    });
  }

  onSubmit() {
    this.submitted = true;

    if (this.myForm.invalid) {
      console.log('invalid');
      return;
    }

    const paymentDetails = this.myForm.value.paymentDetails.map(
      (paymentDetails) => {
        const obj: any = {
          actualAmount: paymentDetails.amount,
          mode: paymentDetails.mode,
          bank: paymentDetails.bank,
          date: paymentDetails.date,
          description: paymentDetails.description,
          transferMode: paymentDetails.transferMode,
          transferPercent: paymentDetails.transferPercent,
          type: 'REFUND',
          // type: paymentDetails.type,
        };

        // if (paymentDetails.transferPercent > 0) {
        //   obj.amount =
        //     paymentDetails.amount -
        //     paymentDetails.amount * (paymentDetails.transferPercent / 100);
        // } else {
        obj.amount = paymentDetails.amount;
        // }

        return obj;
      }
    );

    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      fkSupplierId: this.myForm.value.customer.fkSupplierId,
      fkCustomerId: this.myForm.value.customer.fkCustomerId,
      Total_Amount: this.myForm.value.customer.cost_Price, //if its  payment_Partner is SUPPLIER  then the total amount you need take the supplier amount
      sup_Code: this.myForm.value.customer.sup_Code,
      cust_Code: this.myForm.value.customer.cust_Code,
      payment_Partner: 'SUPPLIER', //CUSTOMER KEY means SUPPLIER to customer refund   // another key is SUPPLIER
      payment_Details: paymentDetails,
    };

    // console.log('obj', obj);
    this.isLoading = true;
    this.supplierService.createVoucher(obj).subscribe((res) => {
      this.isLoading = false;
      if (res.success) {
        Swal.fire({
          title: 'Success',
          text: 'Successfully Created Customer Payment',
          icon: 'success',
        }).then((value) => {
          if (value) {
            this.myForm.reset();
            this.submitted = false;
            this.router.navigate(['/refund']);
          }
        });
      } else {
        Swal.fire({
          title: 'Error',
          text: res.message,
          icon: 'error',
        });
      }
    });
  }
}
